import xinjiang from './modules/xinjiang';
import henan from './modules/henan';
import anhui from './modules/anhui';
import shandong from './modules/shandong';
const provinceDataMap = {
  "新疆": xinjiang,
  "河南": henan,
  "安徽": anhui,
  "山东": shandong
}

export function getProvinceData(provinceName) {
  return provinceDataMap[provinceName] || null
}